.form {
  p {
    font-size: 1.4rem;
    color: $darkGray;
    margin: 2rem 0 0 0;
  }
  legend {
    font-size: 2rem;
    color: $darkGray;
  }
  label {
    font-weight: bold;
    text-transform: uppercase;
    display: block;
  }
  input, textarea, select {
    padding: 1rem;
    display: block;
    width: 100%;
    background-color: $white;
    border: 1px solid $gray;
    border-radius: 1rem;
    margin-bottom: 2rem;
  }

  textarea {
    height: 15rem;
  }
  .form-contact {
    max-width: 30rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    input[type="radio"] {
      margin-bottom: 0;
    }
  }
}