/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: 900;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

legend {
  padding: 0 4px;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.center-content {
  max-width: 80rem;
}

.view-all {
  margin-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.mt-2 {
  margin-top: 2rem;
}

.btn-contact {
  border: none;
  background-color: #E08709;
  color: #FFFFFF;
  cursor: pointer;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
}
.btn-contact:hover {
  background-color: #c77808;
}

.btn-yellow {
  border: none;
  background-color: #E08709;
  color: #FFFFFF;
  cursor: pointer;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
}
.btn-yellow:hover {
  background-color: #c77808;
}

.btn-green {
  border: none;
  background-color: #71B100;
  color: #FFFFFF;
  cursor: pointer;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
}
.btn-green:hover {
  background-color: #619800;
}

.btn-red {
  border: none;
  background-color: rgb(197, 4, 4);
  color: #FFFFFF;
  cursor: pointer;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
}
.btn-red:hover {
  background-color: #ac0303;
}

.btn-green-block {
  border: none;
  background-color: #71B100;
  color: #FFFFFF;
  cursor: pointer;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  width: 100%;
}
.btn-green-block:hover {
  background-color: #619800;
}

.btn-red-block {
  border: none;
  background-color: rgb(197, 4, 4);
  color: #FFFFFF;
  cursor: pointer;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  width: 100%;
}
.btn-red-block:hover {
  background-color: #ac0303;
}

.dark {
  background-color: #1a1a1a;
}
.dark p, .dark label, .dark tbody {
  color: #FFFFFF !important;
}
.dark h1, .dark h2, .dark h3, .dark h4, .dark blockquote, .dark legend {
  color: #e1e1e1 !important;
}
.dark .feature-icons li img {
  filter: invert(0.7);
}
.dark .advertisement {
  background-color: #333333;
  border: 1px solid #e1e1e1;
}

.no-webp .start {
  background-image: url(../img/header.jpg);
}

.webp .start {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0;
}
.header.start {
  background-position: center center;
}
@media (min-width: 768px) {
  .header.start {
    min-height: 70rem;
  }
}

.header-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header-container {
    min-height: 70rem;
  }
}
.header-container h1 {
  color: #FFFFFF;
  text-align: left;
  font-weight: bold;
  max-width: 60rem;
}

.footer {
  padding: 5rem 0;
  background-color: #333333;
}
.footer .footer-container {
  padding-top: 5rem;
}
.footer .navigation {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.right {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .right {
    align-items: end;
  }
}
.right .dark-mode-btn {
  width: 3rem;
  height: 3rem;
  filter: invert(1);
  cursor: pointer;
}

.bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem 0 1rem 0;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.navigation {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.5s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navigation {
    display: flex;
    opacity: 1;
    visibility: visible;
    align-items: center;
    gap: 2rem;
  }
}
.navigation a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.8rem;
  display: block;
  text-align: center;
  transition: color 0.5s ease-in-out;
}
.navigation a:hover {
  color: #71B100;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 480px) {
  .about-icons {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
@media (min-width: 768px) {
  .about-icons {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon {
  display: flex;
  gap: 2rem;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.icon img {
  width: 10rem;
  height: 10rem;
  object-fit: contain;
}
.icon h3 {
  text-transform: uppercase;
}

.container-advertisements {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

@media (min-width: 768px) {
  .container-advertisements {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.advertisement {
  background-color: #e1e1e1;
  border: 1px solid #eeeeee;
}
.advertisement .info-advertisement {
  padding: 2rem;
}
.advertisement h3 {
  text-transform: uppercase;
  font-size: 2rem;
  text-align: start !important;
}
.advertisement h3, .advertisement p {
  margin: 0;
}
.advertisement .price {
  color: #71B100;
  font-weight: bold;
  font-size: 3rem;
}
.advertisement .feature-icons {
  display: flex;
  list-style: none;
  max-width: 40rem;
  padding: 0;
}
.advertisement .feature-icons li {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.advertisement .feature-icons li img {
  flex: 0 0 4rem;
  width: 4rem;
  height: 4rem;
  object-fit: contain;
}
.advertisement .feature-icons li p {
  font-weight: 500;
}

.no-webp .image-contact {
  background-image: url(../img/encuentra.jpg);
}

.webp .image-contact {
  background-image: url(../img/encuentra.webp);
}

.image-contact {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.image-contact h2 {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 3rem;
}
.image-contact p {
  color: #FFFFFF;
  font-size: 2rem;
  text-align: center;
}

@media (min-width: 768px) {
  .inferior-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
}
.blog-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.blog-card {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .blog-card {
    flex-direction: row;
    gap: 3rem;
  }
}

.info-blog a {
  color: #000;
  display: block;
}
@media (min-width: 768px) {
  .info-blog a {
    margin-top: 0;
  }
}
.info-blog h4 {
  margin: 0;
  line-height: 1.4;
}
.info-blog h4::after {
  content: "";
  display: block;
  width: 20rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.info-blog span {
  color: #E08709;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  position: relative;
  border-radius: 2rem;
}
.testimonial p {
  margin: 0;
  line-height: 1.5;
  text-align: end;
  font-weight: 700;
  font-size: 2.8rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -3rem;
  top: 0;
}

.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  font-weight: bold;
  text-transform: uppercase;
  display: block;
}
.form input, .form textarea, .form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 15rem;
}
.form .form-contact {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form .form-contact input[type=radio] {
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .about-content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.about-text blockquote {
  font-weight: 700;
  font-size: 2.2rem;
  margin: 0;
}

.errors {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.success {
  margin-top: 2rem;
  background-color: #71B100;
}

.alert {
  padding: 0.5rem;
  text-align: center;
  color: white;
  font-weight: bold;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 10px;
}
.alert.error {
  background-color: rgb(197, 4, 4);
}

table {
  width: 100%;
  margin-top: 4rem;
  border-spacing: 0;
}
table thead {
  background-color: #71B100;
}
table thead th {
  padding: 1rem;
  color: #FFFFFF;
}
table .property-image {
  width: 10rem;
}
table tbody td {
  text-align: center;
}
table tbody td img {
  margin: 0 auto;
}
table tbody td a {
  margin-bottom: 1rem;
}

@media screen and (max-width: 768px) {
  .properties {
    overflow-x: auto;
  }
}
.image-small {
  width: 25rem;
}